
body {
  margin: 0;
  padding: 150px 0 0 0;
  font-family: 'Roboto', sans-serif;
  background-color: #dfdce3;
  height: 100%;
  position: relative;
}

.navbar {
  background-color: #1f2833;
  font: bold;
  color: white;
  padding: 1em;
}
.nav-link {
  color: white;
}
.navbar-brand {
  color: white;
}

.img-small {
  width: 100px;
}

.content-div {
  padding: 50px;
  -ms-flex-pack: justify;
      justify-content: space-between;
}

.bold {
  color: fuchsia;
}

.main-container {
  min-height: 100vh; /* will cover the 100% of viewport */
  overflow: hidden;
  display: block;
  position: relative;
  padding-bottom: 100px; /* height of your footer */
}

footer {
  position: absolute;
  background-color: #c5c6c7;
